import React from 'react';
import logo from './logo.svg';
import './App.css';
import { useEffect, useState, useCallback } from "react";
import { useDropzone } from 'react-dropzone'
import { projectStorage, projectFirestore, timeStamp } from './firebase/config';
import $ from 'jquery';

function App() {
  const [files, setFiles] = useState<any[]>([]);
  const types = ['image/png', 'image/jpg', 'image/jpeg', 'image/gif', 'image/GIF']
  const [progress, setProgress] = useState(0)
  const [url, setUrl] = useState<any>(null)
  const [docs, setDocs] = useState<any[]>([]);

  const [numberItems, setNumberItems] = useState<number>(0);
  const [pages, setPages] = useState<number[]>([]);
  const [currentPage, setCurrentPage] = useState<number>(1);

  const [isUploading, setIsUploading] = useState<boolean>(false)
  const [isSetting, setIsSetting] = useState<boolean>(false)

  const onDrop = useCallback(async acceptedFiles => {
    // Do something with the files
    acceptedFiles.forEach((element: any) => {
      //getBase64(element)
      if (element && types.includes(element.type)) {
        setFiles(prev => [...prev, element])
      }
      else {
        setFiles([])
        return alert("No es un archivo permitido!!!")
      }
    });
    setIsSetting(true);
  }, [])

  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop, noClick: true })

  const useStorage = () => {
    setIsSetting(false);
    setIsUploading(true);

    files.forEach((element: any) => {
      const storageRef = projectStorage.ref(element.name)
      const collectionRef = projectFirestore.collection('images');

      storageRef.put(element).on('state_changed', (snap) => {
        let percentage = (snap.bytesTransferred / snap.totalBytes) * 100;
        console.log(percentage)
        setProgress(percentage);
        if (percentage == 100) {
          setProgress(0)
          setIsUploading(false);
        }
      }, (err) => {
        alert("Algo ha salido mal en la subida, avisar!")
      }, async () => {
        const name = (await storageRef.getMetadata()).name
        const url = await storageRef.getDownloadURL();
        const createdAt = timeStamp();
        collectionRef.add({ url, createdAt, name })
      })
    });

    setTimeout(() => {
      setDocs([])
      const unsub = projectFirestore.collection('images')
        .orderBy('createdAt', 'desc')
        .onSnapshot((snap) => {
          let documents: any = []
          let cont = 0
          snap.forEach(doc => {
            documents.push({ ...doc.data() })
            cont += 1
          })
          setDocs(documents);
          setNumberItems(cont)
          getPages(cont)
        })
    }, 1200);
  }

  useEffect(() => {
    (async () => {
      try {
        setDocs([])
        const unsub = projectFirestore.collection('images')
          .orderBy('createdAt', 'desc')
          .onSnapshot((snap) => {
            let documents: any = []
            let cont = 0
            snap.forEach(doc => {
              documents.push({ ...doc.data() })
              cont += 1
            })
            setDocs(documents);
            setNumberItems(cont)
            getPages(cont)
          })
      } catch (error) {
        console.log(error)
      }
    })();
  }, []);

  function deleteImage(image: any, e: any) {
    console.log(e.target)
    console.log(image.url)

    let imageRef = projectStorage.refFromURL(image.url);
    imageRef.delete();

    let imageDoc = projectFirestore.collection('images')
    var query = imageDoc.where('name', '==', image.name).get()
      .then(snapshot => {
        snapshot.forEach(doc => {
          console.log(doc.id, '=>', doc.data());
          projectFirestore.collection('images').doc(doc.id).delete();
        });
      })
      .catch(err => {
        console.log('Error getting documents', err);
      });

    setTimeout(() => {
      projectFirestore.collection('images')
        .orderBy('createdAt', 'desc')
        .onSnapshot((snap) => {
          let documents: any = []
          let cont = 0
          snap.forEach(doc => {
            documents.push({ ...doc.data() })
            cont += 1
          })
          setDocs(documents);
          setNumberItems(cont)
          getPages(cont)
        })
    }, 1200);
  }

  function getPages(cont: any) {
    let pag = Math.floor(cont / 15) + 1;

    let arrPages = []
    for (let i = 1; i <= pag; i++) {
      arrPages.push(i)
    }
    setPages(arrPages)
  }

  function setPage(e: any, number: any) {
    setCurrentPage(number)
    let arr = []

    Array.from(e.currentTarget.parentElement.children).forEach((element:any) => {
      element.classList.add('change')
      e.currentTarget.classList.remove('active')
      console.log(element)
    });
    e.currentTarget.classList.remove('change')
    e.currentTarget.classList.toggle('active')
  }

  return (
    <div className="container">

      <div className="dragdrop">
        <div className='je' {...getRootProps()}>
          <input {...getInputProps()} />
          {
            isDragActive ?
              <p>Arrastra imagenes aqui ...</p> :
              (<>
                <p>Arrastra imagenes aqui</p>
                {isSetting ?
                  <>
                    <p>Número de Archivos a subir: {files.length}</p>
                    <span className="upload-btn" onClick={useStorage}>Subir</span>
                  </> : <></>
                }
                {isUploading ?
                  <>
                    <p>Subiendo {files.length} a la nube</p>
                    <p>Progreso: {progress}</p>
                  </> : <></>
                }
              </>)
          }
        </div>
      </div>

      <div className="container-2">
        <div className='left'>
          <p>filtros</p>
        </div>
        <div className="right-content">
          <div className="pages">
            <div className='page'>{pages.map((page: any, index: any) =>
              <span className='indexpage' key={index} onClick={(e) => setPage(e, page)}>{page}</span>
            )}</div>
          </div>
          <div className="right">
            {docs && (docs.filter((item, index) => ((index >= (pages[currentPage - 1] - 1) * 15) && (index < pages[currentPage - 1] * 15))).map((doc: any, indexUser: any) =>
              <div className="card-stake" key={indexUser}>
                <img className="prueba" src={doc.url} alt="test" />
                <span className='delete' onClick={(e) => deleteImage(doc, e)}>delete</span>
              </div>
            ))
            }
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
